.App {
  text-align: justify;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: black;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(32, 32, 32);
}
nav ul{
  border-bottom: 6px solid black; 
  display: flex;
  justify-content: center;
  margin: 0;
  text-align: center; 
  overflow:hidden; 
  background: white;
  list-style-type: none;
  
}
nav ul li a{
  float: left;
  width: 100px;
  padding: 8px 30px;
  text-align: center;
  text-decoration: none;
  color: black;
  font-size: 25px;
  transition: all 250ms ease-in-out;
} 
nav ul li a:hover{
  background-color: black;
  color: white;
}
.App h1{
  font-size: 40px;
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.ele{
  margin: 0 20px;
  margin: auto;
  width: 60%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.list{
  text-decoration: none;
  color: black;
  text-align: justify;
}
.list1{
  width: 70%;
  margin: auto;
  border-bottom: 1px solid black;
  margin-bottom: 30px;
}
.list h3{
  margin-bottom: 0px;
}
.list p{
  margin-top: 10px;
}

h1{
  border-bottom: 1px solid black;
  width: 60%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 40px;
}
button{
  color: white;
  background-color: black;
  padding: 12px;
  border: none;
  border-radius: 5px;
}
.upvotes-section p{
  display: inline;
  margin-left: 40px;
}